.reserve-form {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding-top: 3vh;
    height: 20%;
    width: 44vh;
    background-color: #262726;
    /* transform: translateY(12.2vh); */
    border-radius: 5px;
}

.data-reserve-form {
    display: flex;
    align-content: center;
    justify-content: center;
}

.background-register-customer {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 20vh;
    width: 44vh;
    background-color: #262726;
    /* transform: translateY(15vh); */
}

.body-reserve-core, .body-customer-crud {
    height: 100vh;
    width: 100%;
}

.register-customer-form {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}


