.body-customer-crud {
    height: 100vh;
    width: 100%;
}

.data-crud-customer-form {
    display: flex;
    background-color: #272727;
    border: 0.05vw solid #505051;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.text-field, .data-crud-customer-background {
    display: flex;
    align-content: center;
    justify-content: center;
}