.body-employee-crud {
    height: 100vh;
    justify-content: center;
    /* justify-items: center; */
    align-items: center;
    /* align-content: center; */
}

.data-crud-form {
    background-color: #272727;
    border: 0.05vw solid #505051;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.data-crud-role-form {
    width: 59vh;
    height: 30vh;
    background-color: #272727;
    border: 0.05vw solid #505051;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.text-field, .data-crud-background {
    display: flex;
    align-content: center;
    justify-content: center;
}
